<!---->
<template>
  <main class="article">
    <header-nav ></header-nav>
    <!---->
    <article class="article-detail project-detail">
      <h1 class="article-title">
        <div class="logo"><img :src="info.logo" alt=""></div>
        <div class="name">{{info.extensionTitle}}</div>
      </h1>
      <div class="article-info">
        <a :href="info.homepage"><i class="iconfont icon-gongsiguanwang"></i></a>
        <a :href="info.document"><i class="iconfont icon-wendang"></i></a>
      </div>
      <div class="article-content" v-html="info.body"></div>
    </article>
    <div class="hr-title"></div>
    <div class="project-attr">
      {{info.license}} <br>
      {{info.languages}} <br>
      {{info.os}} <br>
      {{info.recordtime}}
    </div>
  </main>
</template>
<script type="text/ecmascript-6">
  import headerNav from '../../components/header-nav/header-nav.vue'
  import api from  '../../common/js/api/api'

  export default {
    name: 'ProjectDetail',
    data() {
      return {
        info:{}
      }
    },
    components:{
      headerNav
    },
    mounted(){
      this.getArticleInfo();
      this.$store.commit('footerhide');
    },
    methods:{
      //
      getArticleInfo(){
        let token = localStorage.getItem('access_token');;
        let id = this.$route.params.id;
        this.$http.post(api.project.detail,{
          'ident':id,
          'access_token':token
        }).then(res =>{
          this.info = res.data
        })
      }
    }
  }

</script>
<style lang="scss">

</style>



